@import url(https://fonts.googleapis.com/css2?family=Asap&display=swap);
.table-layout {
  overflow: auto;
}
.table-layout .dataTables_wrapper {
  overflow: auto;
  min-width: 450px;
}
.table-layout .dataTables_wrapper .row {
  margin-left: 0;
  margin-right: 0;
}
.table-layout .dataTables_wrapper .row:first-child label {
  font-size: 12px;
}

.table-layout .dataTables_info {
  font-size: 12px;
}
.table-layout .dataTables_wrapper .table td,
.table-layout .dataTables_wrapper .table th {
  font-size: 12px;
  padding: 10px;
  font-family: sans-serif;
}
.table-layout .dataTables_wrapper .table th {
  font-weight: 700;
}
.table-layout .dataTables_wrapper .row:nth-child(3) {
  -webkit-align-items: center;
          align-items: center;
  margin-top: -10px;
}
.table-layout .dataTables_wrapper .pagination .page-item .page-link {
  font-size: 0.7rem;
}

.table-layout::-webkit-scrollbar {
  width: 10px;
  height: 5px;
}

/* Track */
.table-layout::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
.table-layout::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
.table-layout::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.rounded-border {
  border-radius: 6px;
  cursor: pointer;
}
@media screen and (max-width: 768px) {
  .table-layout .dataTables_wrapper .row:first-child div:nth-child(2) {
    -webkit-justify-content: flex-start !important;
            justify-content: flex-start !important;
  }
}

.form-control.custom-input:focus,
.form-control.custom-input:active {
  box-shadow: 0 0 2px #3f6296eb;
  font-family: sans-serif;
}
.form-control.custom-input {
  font-size: 14px;
}
.custom-label {
  font-size: 14px;
}

.dashboard-item {
  height: 100px;
  margin: 3px 0;
  border-radius: 4px;
}
.content {
  padding: 0.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}
.left .label {
  font-weight: bold;
  font-family: sans-serif;
  font-size: 1.4rem;
  color: white;
}
.content .right svg {
  width: 3rem;
  height: 3rem;
}
.left p:nth-child(2) {
  font-family: sans-serif;
  font-weight: 400;
  color: white;
  font-size: 0.9rem;
}

.profile p span {
  font-size: 12px;
  border-radius: 10px;
  color: white;
}
.profile p {
  font-family: sans-serif;
  font-size: 14px;
  font-weight: 600;
}
.form-group label {
  font-size: 14px;
}

.btn.custom-btn {
  font-size: 0.8rem;
  padding: 0.5rem 1.5rem;
}

.btn.custom-btn:active,
.btn.custom-btn:focus,
.btn.custom-btn:hover {
  box-shadow: 0 0 0 black !important;
}
.btn.custom-btn-primary,
.btn.custom-btn-primary:hover,
.btn.custom-btn-primary:focus,
.btn.custom-btn-primary:active {
  background-color: #3f6296eb !important;
}

.login-card {
  width: 90%;
  margin: 0px auto;
  position: relative;
  top: 4%;
  border-radius: 6px;
  max-width: 400px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}
.login-card form {
  position: relative;
  top: 5%;
}
.password-field {
  position: relative;
}
.password-field input {
  padding-left: 7%;
}
.password-field svg {
  position: absolute;
  top: 50%;
  left: 2%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
}
.forget-link {
  font-family: sans-serif;
  font-size: 0.8rem;
  text-decoration: underline;
  font-weight: 700;
}
.password.invalid-feedback {
  display: block;
}
.full-height {
  height: 100vh;
  background-color: #a1a29f1a;
}

.sidebar {
  height: 100vh;
  width: var(--sidebar-width);
  background-color: black;
  position: fixed;
  top: 0;
  left: 0;
  transition: 0.3s ease;
  z-index: 2;
}
.sidebar.shrink {
  width: var(--sidebar-collapse-width);
}

.sidebar ul {
  padding: 10px;
  list-style: none;
}
.sidebar ul li a {
  padding: 6px 0;
  font-size: 12px;
  display: -webkit-flex;
  display: flex;
  position: relative;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  text-decoration: none;
  font-family: sans-serif;
  font-weight: 600;
  opacity: 0.8;
  transition: 0.2s ease;
}
.sidebar ul li a:hover {
  opacity: 1;
}
.sidebar ul li a span {
  margin-left: 10px;
}
.sidebar ul ul {
  padding: 0;
}
.sidebar ul ul li a svg {
  width: 8px;
  height: 8px;
}
.sidebar .submenu {
  max-height: 0;
  transition: all 0.6s ease-in-out 0s;
  background-color: #65655e;
  overflow: hidden;
}
.submenu.active {
  max-height: 300px;
}
.sidebar ul li a.active {
  opacity: 1;
}
.rotate {
  position: absolute;
  top: 50%;
  height: 12px;
  width: 12px;
  right: 4%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  transition: 0.3s ease-out;
}
.sidebar ul li a.active .rotate {
  top: 30%;
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
}
.sidebar.shrink ul li a span {
  display: none;
}
.sidebar.shrink:hover {
  width: var(--sidebar-width);
}
.sidebar.shrink:hover ul li a span {
  display: block;
}
@media screen and (max-width: 856px) {
  .sidebar {
    width: var(--sidebar-collapse-width);
  }
  .sidebar ul li a span {
    display: none;
  }
  .sidebar:hover {
    width: var(--sidebar-width);
  }
  .sidebar:hover ul li a span {
    display: block;
  }
}
@media screen and (max-width: 576px) {
  .sidebar {
    left: -100%;
  }
  .sidebar.shrink {
    left: 0%;
  }
  .sidebar,
  .sidebar.shrink {
    width: var(--sidebar-width);
  }
  .sidebar.shrink ul li a span,
  .sidebar ul li a span {
    display: block;
  }
}

.topbar {
  border-bottom: 2px solid#29242433;
}
.topbar .content {
  padding: 7px;
  display: -webkit-flex;
  display: flex;
  height: var(--topbar-height);
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.topbar-left {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.topbar-left a {
  margin-left: 7px;
  text-decoration: none;
  color: black;
  opacity: 0.8;
  transition: 0.2s ease-in-out;
}
.topbar-left a:hover {
  color: black;
  opacity: 1;
}
.topbar-left svg {
  cursor: pointer;
}
.custom-notification.btn {
  background-color: transparent !important;
  padding: 0;
  border: 0;
  color: black !important;
  outline: none;
  box-shadow: 0 0 0 transparent !important;
}
.notification.show
  > .custom-notification.btn.btn-secondary.dropdown-toggle:active {
  background-color: white !important;
  box-shadow: 0 0 0 transparent !important;
}
.custom-notification .badge {
  color: red !important;
  background-color: white !important;
  right: 1px;
  box-shadow: 0 0px 0px white !important;
  top: -2%;
  font-size: 10px;
}
.notification-dropdown-menu.dropdown-menu {
  left: -170px;
  min-width: 200px;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.2);
}
.notification-dropdown-menu button {
  font-size: 13px;
  font-family: "Asap", sans-serif;
  transition: 0.2s ease-in-out;
}
.notification-dropdown-menu button:hover {
  background-color: #a5a19c38;
}

.footer {
  margin-top: auto;
  height: 50px;
  padding: 4px 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;

  box-shadow: 0px -1px rgba(0, 0, 0, 0.2);
  border-top: 1px solid #80808054;
}
.footer p {
  margin-bottom: 0px;
}
@media screen and (max-width: 576px) {
  .footer p {
    font-size: 12px;
  }
}

.body {
  margin-left: var(--sidebar-width);
  transition: 0.4s ease-in-out 0s;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.body.shrink {
  margin-left: var(--sidebar-collapse-width);
}
.layout-paper {
  left: -100%;
  height: 100vh;
  width: 100%;
  z-index: 1;
  background-color: #bdadad0a;
  position: fixed;
  top: 0;
}
.layout-paper.shrink {
  left: -100%;
}
.body-content {
  padding: 10px;
  -webkit-flex: 1 1;
          flex: 1 1;
  background-color: #b8b8c12e;
}
@media screen and (max-width: 856px) {
  .body,
  .body.shrink {
    margin-left: var(--sidebar-collapse-width);
  }
  .layout-paper,
  .layout-paper.shrink {
    left: -100%;
  }
}

@media screen and (max-width: 576px) {
  .body,
  .body.shrink {
    margin-left: 0;
  }
  .layout-paper {
    left: -100%;
  }
  .layout-paper.shrink {
    left: 0%;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --sidebar-width: 200px;
  --sidebar-collapse-width: 50px;
  --topbar-height: 50px;
}

